<template>
  <div :style="cuthorn">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "Chamfering",
  props: {
    bgcolor: {
      type: String,
      default: "#fff",
    },
    horn: {
      type: String,
      default: "",
    },
    size: {
      type: String,
      default: "0",
    },
  },
  data() {
    return {
      direction: [
				{ deg: "135deg", horn: "top left" },
        { deg: "-135deg", horn: "top right" },
        { deg: "-45deg", horn: "bottom right" },
        { deg: "45deg", horn: "bottom left" },
      ],
    };
  },
  components: {},
  methods: {},
  computed: {
		// 切div
    cuthorn() {
			let str = `background: ${this.bgcolor};`;
			if(this.horn != '') str += 'background:'
      for (let i = 0; i < 4; i++) {
				let index = this.horn.indexOf(i + 1);
					let item = this.direction[i];
					// console.log(item);
          str += `linear-gradient(${item.deg},${ index != -1 ? 'transparent' : this.bgcolor} ${this.size},${this.bgcolor} 0) ${item.horn}`;
          if (i == 3) {
            str += ";";
          } else {
            str += ",";
          }
			}
      return str + 'background-size: 51% 51%; background-repeat: no-repeat;';
    },
  },
};
</script>

